.standar-tab {
  -webkit-transition:all 0.5s;
  -o-transition:all 0.5s;
  transition:all 0.5s;
}

.standar-tab:hover {
  opacity: 0.5;
}

.animation-div {
    margin-top: 25px;
    font-size: 21px;
    margin-right: 50px;

    -webkit-animation: fadein 1.5s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 1.5s;
}

@keyframes fadein {
  from { opacity: 0; padding-top: 100px; }
  to   { opacity: 1; padding-top: 0px; }
}

